import {
  PRODUCT_LIST_REQUEST,
  PRODUCT_LIST_SUCCESS,
  PRODUCT_LIST_FAIL,
} from "../constants/productConstants";
import Axios from "axios";
import { ThunkDispatch } from "redux-thunk";
import { RootState } from "../store";
import { ProductListAction, Product } from "../types";

type ProductListDispatch = ThunkDispatch<RootState, {}, ProductListAction>;
const listProducts = () => async (dispatch: ProductListDispatch) => {
  try {
    dispatch({ type: PRODUCT_LIST_REQUEST, payload: [] });
    const reponse = await Axios.get("/api/product/mail");
    const data: Product[] = reponse.data;
    dispatch({ type: PRODUCT_LIST_SUCCESS, payload: data });
  } catch (error) {
    dispatch({ type: PRODUCT_LIST_FAIL, payload: [] });
  }
};

export { listProducts };
